export default {
  props: ["ob"],
  data() {
    return {
      accordionList: [1],
      suggestion: {
        name: "",
        email: "",
        title: "",
        content: ""
      }
    };
  },
  methods: {
    submit() {
      if (!this.suggestion.name || !this.suggestion.email || !this.suggestion.title || !this.suggestion.content) {
        this.$message({
          message: "",
          type: "error"
        });
        return;
      } else {
        this.$message({
          message: "",
          type: "success"
        });
      }
    },
    toggle(index) {
      let _checker = false;
      for (let i = 0, a = this.accordionList; i < a.length; i++) {
        if (index === a[i]) {
          this.accordionList.splice(i, 1);
          _checker = true;
          break;
        }
      }

      if (!_checker) this.accordionList.push(index);
    }
  }
};
