  .row {
    float: left;
    width: 100%;
    margin-bottom: 20px;
  }

  .card {
    border: 2px solid $color-white;
    text-align: center;
    padding: 30px;
    min-height: 200px;

    h3 {
      color: $color-yellow13;
    }

    .card-list {
      font-weight: bolder;
    }

    .fa {
      font-size: 4em;
      color: $color-yellow13;
      margin-bottom: 20px;
    }
  }
}

.info-content::after {
